import * as React from 'react';
import { useState, useEffect } from 'react';
import { Link, useHistory, Route, BrowserRouter as Router } from "react-router-dom"
import axios from 'axios';
import { makeStyles } from '@material-ui/core/styles';
import { Button, CardContent, Grid, IconButton, Paper, TextField, InputBase, Dialog, DialogTitle, List, ListItem, CircularProgress, Box, Typography } from '@material-ui/core'
import { useSnackbar } from 'notistack';
import { cloneDeep } from 'lodash';
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';
import DoneIcon from '@material-ui/icons/Done';
import SaveAltIcon from '@material-ui/icons/SaveAlt';
import ZoomInIcon from '@material-ui/icons/ZoomIn';
import ZoomOutIcon from '@material-ui/icons/ZoomOut';
import { TABLE_INFO, PREDEFINED_FIELDS, PROCESS_AVAILABLE_TABLES, EXCEPT_TABLES, PROCESS_OPTIONS } from './DataDef.js'
import addIcon from './img/icon_add_process.svg';
import addItemIcon from './img/icon_add_item.svg';
import deleteIcon from './img/icon_delete_process.svg'

const useStyles = makeStyles({
    title: {
        display: 'flex', 
        gap: 10
    },
    content: {
        width: '100%', 
        display: 'flex', 
        gap: 25, 
        marginTop: 5, 
        marginLeft: 10
    },
    leftPaper: {
        width: '650px', 
        height: '600px', 
        overflow: 'scroll'
    },
    rightPaper: {
        width: '600px', 
        height: '600px', 
        marginTop: 20, 
        padding: 10,
        overflow: 'scroll'
    },
    notePaper: {
        width: '1234px', 
        height: '136px', 
        marginTop: 10, 
        padding: 10,
        overflow: 'scroll'
    },
    dbBtn: {
        marginTop: 10
    },
    prevBtn: {
        width:98, height:23, background:"rgba(23, 62, 139, 1)", color:"white",
        marginTop: 10
    },
    nextBtn: {
        width:98, height:23, background:"rgba(23, 62, 139, 1)", color:"white",
        marginTop: 10, 
        marginLeft: "auto"
    },
    submitTableBtn: {
        width:98, height:23, background:"#DD2818", color:"white",
        marginTop: 10, 
        marginLeft: "auto"
    },
    tableCard: {
        display: 'grid',
        marginTop: 10,
        paddingLeft: 10,
        height: 42,
        gap: 3,
    },
    tableCardGas: {
        width: 120,
        marginTop: 10,
        paddingLeft: 10,
        height: 42,
        gap: 3,
    },
    cardNum: {
        marginLeft: -10,
        fontWeight: 'bold',
        fontSize: 14,
        // textDecoration: 'underline'
    },
    select: {
        width: 80,
        height: 20,
        marginRight: 10,
        fontSize: 8
    },
    preDefinedSelect: {
        height: 20,
        marginTop: 15,
        marginLeft: 3,
        fontSize: 8
    },
    formControl: {
        width: 80,
        height: 20,
        marginTop: '10px',
        marginLeft: '0px',
        "& .MuiOutlinedInput-root .MuiOutlinedInput-notchedOutline": {
          border: '1px solid',
          borderColor: "#707070",
          borderRadius: "4px",
        },
    },
    addItemBtn: {
        marginTop: 15,
        marginLeft: 5,
        width: 20,
        height: 20,
        cursor: 'pointer'
    },
    dateInput: {
        marginTop: 10,
        height: 30
    },
    guide: {
        marginTop: 30, 
        textAlign: "center"
    },
    dashedRect: {
        position:'absolute', 
        border: 'dashed 3px',
        textAlign: 'center'
    },
    leftSide: {

    },
    rightSide: {

    },
    deleteProcessBtn: {
        marginLeft: 'auto',
        cursor: 'pointer'
    },
    addProcessBtn: {
        marginLeft: 'auto',
        cursor: 'pointer'
    },
    secondProcess: {
        borderLeft: '0.2px dashed gray'
    },
    thirdProcess: {
        borderLeft: '0.2px dashed gray'
    },
    secondGasProcess: {
        marginTop:5, 
        paddingTop:5, 
        borderTop: '0.2px dashed gray'
    },
    thirdGasProcess: {
        marginTop:5, 
        paddingTop:5, 
        borderTop: '0.2px dashed gray'
    },
    gasTable: {
        display: "grid", 
        gap: 10, 
        gridTemplateColumns: '140px 130px 130px 120px'
    }
});

export default function Inspector(props) {

    const classes = useStyles();
    const history = useHistory();
    const ocrData = props.ocrData;
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();

    // pdf document 상태 관리
    const [docState, setDocState] = useState({ // pdf page, zoom
        total: 0,
        current: 1,
        zoom: 2.2
    });
    const [rectArea, setRectArea] = useState({ // pdf 점선 사각형 범위
        left: 0,
        top: 0,
        width: 0,
        height: 0,
        
    });
    const onDocumentLoadSuccess = ({numPages}) => { // pdf load 완료 시 호출 -> total page set
        setDocState((prevState) => (
            {...docState,
            total: numPages}
        ))
    };

    // table 상태 관리
    const [tableState, setTableState] = useState({
        list: [], // total table list
        total: 0,
        current: 0,
        finished: [],
        title: "",
        isProcessAvailable: false,
    });

    const [refinedTable, setRefinedTable] = useState({}); // OCR 데이터 최초 로드 및 정제된 테이블
    const [currentTable, setCurrentTable] = useState({}); // 수정용 현재 테이블
    const [clonedTable, setClonedTable] = useState({}); // 추가 프로세스 테이블 1
    const [clonedTable2, setClonedTable2] = useState({}); // 추가 프로세스 테이블 2

    // process 상태 관리
    const [processState, setProcessState] = useState({
        count: 1, // 현재 프로세스 개수
        first: "GTAW", // currentTable
        second: "GTAW", // clonedTable
        third: "GTAW" // clonedTable2
    });

    const [DBState, setDBState] = useState({}); // DB 전송용 정보 저장
    const [isDBSave, setIsDBSave] = useState(false);
    const [loadingDialogOpen, setLoadingDialogOpen] = useState(false);

    const setTable = (process, key, newArr) => {
        if (process === 1) {
            setClonedTable({
                ...clonedTable,
                [key]: newArr
            })
        } else if (process === 2) {
            setClonedTable2({
                ...clonedTable2,
                [key]: newArr
            })
        } else {
            setCurrentTable({
                ...currentTable,
                [key]: newArr
            })
        }
    };
    

    const makeField = (typeStr, arr, key, idx, process, disabled) => { // 테이블 row 내부 field 생성 / process) null: 없음, 1: 첫번째 추가 프로세스, 2: 두번째 추가 프로세스
        switch (typeStr) {
            case "T": // text field
                if (arr[idx] === undefined) { // 해당 항목의 array 비어 있을 경우
                    return <TextField 
                    margin="dense" 
                    inputProps={{style: {fontSize: 10}}} 
                    value={""} 
                    fullWidth 
                    variant="outlined" 
                    disabled={disabled}
                    onChange={e=> {
                        let newArr = arr;
                        while (newArr.length <= idx) {
                            newArr.push("");
                        }
                        newArr[idx] = e.target.value;
                        setTable(process, key, newArr);
                    }}/>
                }
                return <TextField 
                margin="dense" 
                inputProps={{style: {fontSize: 10}}} 
                value={arr[idx]} 
                fullWidth 
                variant="outlined" 
                disabled={disabled}
                onChange={e=> {
                    let newArr = arr;
                    newArr[idx] = e.target.value;
                    setTable(process, key, newArr);
                }}/>
            case "D": // date input
                return <input 
                type="date"
                className={classes.dateInput}
                value={arr[idx]} 
                onChange={e=> {
                    let newArr = arr;
                    newArr[idx] = e.target.value;
                    setTable(process, key, newArr);
                }}/>
            case "P": // predefined selector
                return <select
                onChange={e=> {
                    let newArr = arr;
                    newArr[idx] = e.target.value;
                    setTable(process, key, newArr);
                }}
                className={classes.preDefinedSelect}
                style={{width: tableState.title.includes("408") ? 40 : 60/processState.count+20 }}
                defaultValue={PREDEFINED_FIELDS[key][0]}
                value={arr[idx]}>
                    {PREDEFINED_FIELDS[key].map((item, idx) => {
                        if (idx === 0) return <option value={item} selected>{item}</option>
                        else return <option value={item}>{item}</option>
                    })}
                </select>
            case "PG": // predefined selector for GAS Table
                return <select
                onChange={e=> {
                    let newArr = arr;
                    newArr[idx] = e.target.value
                    if (e.target.value !== "있음") {
                        newArr[1] = e.target.value;
                        newArr[2] = e.target.value;
                        newArr[3] = e.target.value;
                        newArr[5] = e.target.value;
                    } else {
                        newArr[1] = "";
                        newArr[2] = "";
                        newArr[3] = "";
                        newArr[5] = "";
                    }
                    setTable(process, key, newArr);
                }}
                className={classes.preDefinedSelect}
                style={{width: tableState.title.includes("408") ? 40 : 60/processState.count+20 }}
                defaultValue={PREDEFINED_FIELDS[key][0]}
                value={arr[idx]}>
                    {PREDEFINED_FIELDS[key].map((item, idx) => {
                        if (idx === 0) return <option value={item} selected>{item}</option>
                        else return <option value={item}>{item}</option>
                    })}
                </select>
            case "AP": // ADDIBLE predefined selector
                if (arr.length === 1) {
                    arr.push(PREDEFINED_FIELDS[key][0]);
                }
                return (
                <div style={{display: 'flex'}}>
                    <select
                    onChange={e=> {
                        let newArr = arr;
                        newArr[1] = e.target.value;
                        setTable(process, key, newArr);
                    }}
                    className={classes.preDefinedSelect}
                    style={{width: tableState.title.includes("408") ? 40 : 60/processState.count+20 }}
                    defaultValue={PREDEFINED_FIELDS[key][0]}
                    value={arr[1]}>
                        {PREDEFINED_FIELDS[key].map((item) => {
                            return <option value={item}>{item}</option>
                        })}
                    </select>

                    {arr.length >= 3 ? <select
                    onChange={e=> {
                        let newArr = arr;
                        newArr[2] = e.target.value;
                        setTable(process, key, newArr);
                    }}
                    className={classes.preDefinedSelect}
                    style={{width: tableState.title.includes("408") ? 40 : 60/processState.count+20 }}
                    defaultValue={PREDEFINED_FIELDS[key][0]}
                    value={arr[2]}>
                        {PREDEFINED_FIELDS[key].map((item) => {
                            return <option value={item}>{item}</option>
                        })}
                    </select> : null}

                    {arr.length >= 4 ? <select
                    onChange={e=> {
                        let newArr = arr;
                        newArr[3] = e.target.value;
                        setTable(process, key, newArr);
                    }}
                    className={classes.preDefinedSelect}
                    style={{width: tableState.title.includes("408") ? 40 : 60/processState.count+20 }}
                    defaultValue={PREDEFINED_FIELDS[key][0]}
                    value={arr[3]}>
                        {PREDEFINED_FIELDS[key].map((item) => {
                            return <option value={item}>{item}</option>
                        })}
                    </select> : null}

                    {arr.length >= 2 && arr.length <= 3 ? <img 
                    src={addItemIcon} 
                    className={classes.addItemBtn}
                    onClick={() => {
                        let newArr = arr;
                        newArr.push("");
                        setTable(process, key, newArr);
                    }} alt="add icon"/> : null}

                    {arr.length >= 3 && arr.length <= 4 ? <img
                    src={deleteIcon} 
                    className={classes.addItemBtn} 
                    onClick={() => {
                        let newArr = arr;
                        newArr.pop();
                        setTable(process, key, newArr);
                    }} alt="add icon"/> : null}
                </div>)
            default:
                return <CardContent className={classes.cardNum}>{typeStr}</CardContent>
        }
    }

    const makeCard = (key, process) => { // 테이블 row 생성
        const targetTable = process===1 ? clonedTable : process===2 ? clonedTable2 : currentTable; // process에 따른 target table
        const fieldTypeArr = TABLE_INFO[tableState.title][key].split('/'); // 미리 정의된 각 row 구성 요소 array
        const columnTemplate = 
            fieldTypeArr.length <= 1 ? `${355/processState.count}px`
                : fieldTypeArr.length === 2 ? `${180/processState.count}px ${180/processState.count}px`
                : fieldTypeArr.length === 3 ? `${120/processState.count}px ${120/processState.count}px ${120/processState.count}px`
                : fieldTypeArr.length === 4 ? `${90/processState.count}px ${45/processState.count}px ${90/processState.count}px ${45/processState.count}px`
                : fieldTypeArr.length === 5 ? `${70/processState.count}px ${70/processState.count}px ${30/processState.count}px ${70/processState.count}px ${70/processState.count}px`
                : `${50/processState.count}px ${25/processState.count}px ${60/processState.count}px ${25/processState.count}px ${20/processState.count}px ${60/processState.count}px ${25/processState.count}px ${60/processState.count}px ${25/processState.count}px `;
        
        return (<div 
        className={classes.tableCard}
        style={{gridTemplateColumns: columnTemplate}}>
            {fieldTypeArr.map((typeStr, idx) => {
                return makeField(typeStr, targetTable[key], key, idx, process, false)
            })}
        </div>)
    }
    
    const makeGasTable = (process) => { // Gas 테이블 생성
        let targetTable = process===1 ? clonedTable : process===2 ? clonedTable2 : currentTable;
        return (
            <div >
                <div style={{display: 'grid', gridTemplateColumns: `130px 130px 130px`, gridTemplateRows: `150px 40px`}}>
                    <div className={classes.tableCardGas}>
                        <div style={{display: 'grid', gap: 5, gridTemplateColumns: `40px 70px`}}>
                            {makeField("PG", targetTable["Shielding_Gas"], "Shielding_Gas", 0, process, false)}
                            {makeField("T", targetTable["Shielding_Gas"], "Shielding_Gas", 1, process, targetTable["Shielding_Gas"][0] === "NONE" || targetTable["Shielding_Gas"][0] === "N/A")}
                        </div>
                        <div style={{display: 'grid', gridTemplateColumns: `120px`}}>
                            {makeField("T", targetTable["Shielding_Gas"], "Shielding_Gas", 2, process, targetTable["Shielding_Gas"][0] === "NONE" || targetTable["Shielding_Gas"][0] === "N/A")}
                        </div>
                        <div style={{display: 'grid', gridTemplateColumns: `40px 15px 40px 20px`}}>
                            {makeField("T", targetTable["Shielding_Gas"], "Shielding_Gas", 3, process, targetTable["Shielding_Gas"][0] === "NONE" || targetTable["Shielding_Gas"][0] === "N/A")}
                            {makeField("~", targetTable["Shielding_Gas"], "Shielding_Gas", 4, process, targetTable["Shielding_Gas"][0] === "NONE" || targetTable["Shielding_Gas"][0] === "N/A")}
                            {makeField("T", targetTable["Shielding_Gas"], "Shielding_Gas", 5, process, targetTable["Shielding_Gas"][0] === "NONE" || targetTable["Shielding_Gas"][0] === "N/A")}
                            {makeField("mm", targetTable["Shielding_Gas"], "Shielding_Gas", 6, process, targetTable["Shielding_Gas"][0] === "NONE" || targetTable["Shielding_Gas"][0] === "N/A")}
                        </div>
                    </div>
                    <div className={classes.tableCardGas}>
                        <div style={{display: 'grid', gap: 5, gridTemplateColumns: `40px 70px`}}>
                            {makeField("PG", targetTable["Backing_Gas"], "Backing_Gas", 0, process, targetTable["Backing_Gas"][0] === "NONE" || targetTable["Backing_Gas"][0] === "N/A")}
                            {makeField("T", targetTable["Backing_Gas"], "Backing_Gas", 1, process, targetTable["Backing_Gas"][0] === "NONE" || targetTable["Backing_Gas"][0] === "N/A")}
                        </div>
                        <div style={{display: 'grid', gridTemplateColumns: `120px`}}>
                            {makeField("T", targetTable["Backing_Gas"], "Backing_Gas", 2, process, targetTable["Backing_Gas"][0] === "NONE" || targetTable["Backing_Gas"][0] === "N/A")}
                        </div>
                        <div style={{display: 'grid', gridTemplateColumns: `40px 15px 40px 20px`}}>
                            {makeField("T", targetTable["Backing_Gas"], "Backing_Gas", 3, process, targetTable["Backing_Gas"][0] === "NONE" || targetTable["Backing_Gas"][0] === "N/A")}
                            {makeField("~", targetTable["Backing_Gas"], "Backing_Gas", 4, process, targetTable["Backing_Gas"][0] === "NONE" || targetTable["Backing_Gas"][0] === "N/A")}
                            {makeField("T", targetTable["Backing_Gas"], "Backing_Gas", 5, process, targetTable["Backing_Gas"][0] === "NONE" || targetTable["Backing_Gas"][0] === "N/A")}
                            {makeField("mm", targetTable["Backing_Gas"], "Backing_Gas", 6, process, targetTable["Backing_Gas"][0] === "NONE" || targetTable["Backing_Gas"][0] === "N/A")}
                        </div>
                    </div>
                    <div className={classes.tableCardGas}>
                        <div style={{display: 'grid', gap: 5, gridTemplateColumns: `40px 70px`}}>
                            {makeField("PG", targetTable["Trailing_Gas"], "Trailing_Gas", 0, process, targetTable["Trailing_Gas"][0] === "NONE" || targetTable["Trailing_Gas"][0] === "N/A")}
                            {makeField("T", targetTable["Trailing_Gas"], "Trailing_Gas", 1, process, targetTable["Trailing_Gas"][0] === "NONE" || targetTable["Trailing_Gas"][0] === "N/A")}
                        </div>
                        <div style={{display: 'grid', gridTemplateColumns: `120px`}}>
                            {makeField("T", targetTable["Trailing_Gas"], "Trailing_Gas", 2, process, targetTable["Trailing_Gas"][0] === "NONE" || targetTable["Trailing_Gas"][0] === "N/A")}
                        </div>
                        <div style={{display: 'grid', gridTemplateColumns: `40px 15px 40px 20px`}}>
                            {makeField("T", targetTable["Trailing_Gas"], "Trailing_Gas", 3, process, targetTable["Trailing_Gas"][0] === "NONE" || targetTable["Trailing_Gas"][0] === "N/A")}
                            {makeField("~", targetTable["Trailing_Gas"], "Trailing_Gas", 4, process, targetTable["Trailing_Gas"][0] === "NONE" || targetTable["Trailing_Gas"][0] === "N/A")}
                            {makeField("T", targetTable["Trailing_Gas"], "Trailing_Gas", 5, process, targetTable["Trailing_Gas"][0] === "NONE" || targetTable["Trailing_Gas"][0] === "N/A")}
                            {makeField("mm", targetTable["Trailing_Gas"], "Trailing_Gas", 6, process, targetTable["Trailing_Gas"][0] === "NONE" || targetTable["Trailing_Gas"][0] === "N/A")}
                        </div>
                    </div>
                    <div style={{gridRowStart:2, gridRowEnd:3, gridColumnStart:1, gridColumnEnd:4, width: 360, marginLeft: 10}}>
                        {makeField("T", process===1 ? clonedTable["Others"] : process===2 ? clonedTable2["Others"] : currentTable["Others"], "Others", 0, process, false)}
                    </div>
                </div>
                
            </div>
        )

    }

    const moveTable = (to) => { // 이전, 다음 테이블 이동 (to: -1 OR +1)
        const toMove = tableState.current + to;
        if (toMove >= 0 && toMove <=tableState.total-1) {
            setTableState(prevState => ({
                ...prevState,
                current: toMove,
                title: prevState.list[toMove],
                isProcessAvailable: false
            }));
            const loadTable = cloneDeep(ocrData[tableState.list[toMove]]["fields"]);
            Object.keys(loadTable).forEach((key)=> { // 인덱스 0으로 전체 내용 모으기 및 나머지 필드 빈칸 처리
                loadTable[key][0] = loadTable[key].join(' ')
                for (let i=1; i<loadTable[key].length; i++) {
                    loadTable[key][i] = ""
                }
            });
            setRefinedTable(cloneDeep(loadTable));
            setCurrentTable(loadTable); // 테이블 load
            setRectArea({...rectArea, // 점선 사각형 범위 계산
                top: 800*ocrData[tableState.list[toMove]]["area"]["top"],
                left: 550*ocrData[tableState.list[toMove]]["area"]["left"],
                width: 600*(ocrData[tableState.list[toMove]]["area"]["right"] - ocrData[tableState.list[toMove]]["area"]["left"]),
                height: 800*(ocrData[tableState.list[toMove]]["area"]["bottom"] - ocrData[tableState.list[toMove]]["area"]["top"]),
            });
            setProcessState({ // process 상태 초기화
                count: 1,
                first: "GTAW",
                second: "GTAW",
                third: "GTAW"
            });
            if (PROCESS_AVAILABLE_TABLES.includes(tableState.list[toMove])) { // process 추가 가능 테이블 확인
                setTableState(prevState => ({
                    ...prevState,
                    isProcessAvailable: true
                }));
            }
        }
    }

    const addProcess = () => { // process 추가
        if (processState.count === 1) {
            setClonedTable(cloneDeep(refinedTable));
            setProcessState({
                ...processState,
                count: 2
            });
        } else if (processState.count === 2) {
            setClonedTable2(cloneDeep(refinedTable));
            setProcessState({
                ...processState,
                count: 3
            });
        }
    }

    const deleteProcess = (position) => { // process 삭제
        if (position === 2) { // 두번째 process 삭제
            if (processState.count === 3) { // 중간 process 삭제
                setClonedTable(clonedTable2);
                setProcessState({
                    ...processState,
                    count: 2,
                    second: processState.third
                });
            }
            else if (processState.count === 2) {
                setProcessState({
                    ...processState,
                    count: 1
                });
            }
        } else if (position === 3) {
            setProcessState({
                ...processState,
                count: 2
            });
        }
    }

    const submitTable = () => { // 수정된 테이블 DB state 저장
        if (tableState.finished.indexOf(tableState.current) === -1) { // finished 리스트에 없는 경우 현재 table 인덱스 추가
            setTableState({
                ...tableState,
                finished: tableState.finished.concat(tableState.current)
            });
        }
        if (tableState.isProcessAvailable) { // 프로세스 추가 가능 테이블 확인
            if (processState.count === 2) {
                setDBState((prevState) => ({
                    ...prevState,
                    [tableState.title]: [{
                        "fields": {...cloneDeep(clonedTable)},
                        "Process": processState.second,
                    },{
                        "fields": {...cloneDeep(currentTable)},
                        "Process": processState.first,
                    }]
                }));
            } else if (processState.count === 3) {
                setDBState((prevState) => ({
                    ...prevState,
                    [tableState.title]: [{
                        "fields": {...cloneDeep(clonedTable2)},
                        "Process": processState.third,
                    },{
                        "fields": {...cloneDeep(clonedTable)},
                        "Process": processState.second,
                    },{
                        "fields": {...cloneDeep(currentTable)},
                        "Process": processState.first,
                    }]
                }));
            } else {
                setDBState((prevState) => ({
                    ...prevState,
                    [tableState.title]: [{
                        "fields": {...cloneDeep(currentTable)},
                        "Process": processState.first,
                    }]
                }));
            }
        } else {
            setDBState((prevState) => ({ // 추가 프로세스 없음
                ...prevState,
                [tableState.title]: [{
                    "fields": {...cloneDeep(currentTable)},
                }]
            }));
        }
    }

    const saveDB = () => { // 서버 DB 저장 요청
        setLoadingDialogOpen(true)
        axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "DB",
                "data": [DBState]
        })
        .then((res) => {
            setIsDBSave(true);
            setTimeout(() => {
                setLoadingDialogOpen(false)
                history.push('/')
            }, 5000);
        })
        .catch((err) => {
            console.log(err);
            enqueueSnackbar('에러 발생', {variant: 'error'})
        })
    }

    
    useEffect(() => { // 최초 페이지 진입 시 데이터 세팅
        if (props.ocrData !== null) {
            const sortedTableKeys = Object.keys(ocrData).filter(key => !EXCEPT_TABLES.includes(key)).sort(); // 테이블 key 정렬
            setTableState({
                total: sortedTableKeys.length,
                current: 0,
                finished: [],
                title: sortedTableKeys[0],
                isProcessAvailable: false,
                list: sortedTableKeys
            });
            const loadTable = cloneDeep(ocrData[sortedTableKeys[0]]["fields"]); // 인덱스 0 테이블 로드 및 정제
            Object.keys(loadTable).forEach((key)=> { // 인덱스 0으로 전체 내용 모으기 및 나머지 필드 빈칸 처리
                loadTable[key][0] = loadTable[key].join(' ')
                for (let i=1; i<loadTable[key].length; i++) {
                    loadTable[key][i] = ""
                }
            })
            setRefinedTable(cloneDeep(loadTable));
            setCurrentTable(loadTable);
            setProcessState({
                ...processState,
                count: 1
            })
            setRectArea({...rectArea,
                top: 800*ocrData[sortedTableKeys[0]]["area"]["top"],
                left: 600*ocrData[sortedTableKeys[0]]["area"]["left"],
                width: 600*(ocrData[sortedTableKeys[0]]["area"]["right"] - ocrData[sortedTableKeys[0]]["area"]["left"]),
                height: 800*(ocrData[sortedTableKeys[0]]["area"]["bottom"] - ocrData[sortedTableKeys[0]]["area"]["top"]),
            })
            setDBState({ // DB state에 company 필드 임의 삽입
                "Company": ocrData["Company"]
            });
        }
    }, []);

    return (
        <>
        <div className={classes.title}>
            <Box fontWeight="fontWeightBold" fontSize={24} m={1}>
                Inspect -  {props.userFile ? props.userFile.name : "파일 없음"}
            </Box>
            <Button variant="outlined" color="primary" className={classes.dbBtn} disabled={tableState.finished.length !== tableState.total} onClick={saveDB}>
                <SaveAltIcon fontSize='small'/>&nbsp;&nbsp;DB 저장
            </Button>
        </div>
        <Dialog open={loadingDialogOpen} aria-labelledby="loading-dialog" maxWidth='xs' fullWidth={true}>
            <DialogTitle id="loading-dialog">DB Save</DialogTitle>
            <List style={{ margin:15 }}>
                <ListItem>
                    <Typography variant="h6" noWrap >Saving to DB..&nbsp;&nbsp;</Typography>
                    {!isDBSave ? <CircularProgress size={20}/> : <DoneIcon />}
                </ListItem>
            </List>
        </Dialog>
        <div className={classes.content}>

            <div className={classes.leftSide}>
                <Box>
                    OCR 결과
                    <IconButton 
                        onClick={() => {
                            setDocState((prevState) => ({
                                ...prevState,
                                zoom: docState.zoom + 1
                            }))
                        }}>
                        <ZoomInIcon fontSize="small"/>
                    </IconButton>
                    <IconButton 
                        onClick={() => {
                            setDocState((prevState) => ({
                                ...prevState,
                                zoom: docState.zoom - 1
                            }))
                        }}>
                        <ZoomOutIcon fontSize="small"/>
                    </IconButton>
                </Box>

                <Paper elevation={1} className={classes.leftPaper}>
                    <Document 
                        file={props.userFile}
                        onLoadError={console.error}
                        onLoadSuccess={onDocumentLoadSuccess}
                        noData={<div className={classes.guide}>PDF 파일이 업로드되지 않았습니다.<br/>좌측 업로드 메뉴에서 파일을 업로드해주세요.</div>}>
                        <Page height={400} pageNumber={docState.current} scale={docState.zoom}>
                            <div 
                                className={classes.dashedRect}
                                style={{
                                    left: rectArea.left*docState.zoom/2, 
                                    top: rectArea.top*docState.zoom/2, 
                                    width: rectArea.width*docState.zoom/2, 
                                    height: rectArea.height*docState.zoom/2, 
                                }}/>
                        </Page>
                    </Document>
                </Paper>
                
                <Grid container>
                    <Button 
                        className={classes.prevBtn} 
                        variant="outlined" 
                        disabled={docState.current === 1 ? true : false} 
                        onClick={() => {
                            setDocState((prevState) => ({
                                ...prevState,
                                current: docState.current-1
                            }))
                        }}>
                        이전페이지
                    </Button>
                    <Button 
                        className={classes.nextBtn} 
                        variant="outlined" 
                        disabled={docState.current === docState.total || docState.total === null ? true : false} 
                        onClick={() => {
                            setDocState((prevState) => ({
                                ...prevState,
                                current: docState.current+1
                            }))
                        }}>
                        다음페이지
                    </Button>
                </Grid>
            </div>
            
            <div className={classes.rightSide}>
                <Box>Table - {tableState.current+1} / {tableState.total} &nbsp; | &nbsp;남은 수정 테이블 : {tableState.total-tableState.finished.length}</Box>

                {!tableState.title.includes("408") ? <div>
                    <Paper elevation={1} className={classes.rightPaper}>
                    <div style={{display: "flex"}}>
                        <div style={{display: "grid", gap: 10, gridTemplateColumns: processState.count===1 ? '140px 360px': processState.count===2 ? '140px 190px 180px' :'140px 130px 130px 120px'}}>
                            <Box>{tableState.title}&nbsp;&nbsp;&nbsp;&nbsp;</Box>
                            {tableState.isProcessAvailable ?
                                <select
                                    onChange={(e) => {
                                        setProcessState((prevState) => ({...prevState, first: e.target.value}));
                                    }}
                                    className={classes.select}
                                    defaultValue={"GTAW"}
                                    value={processState.first}>
                                    {PROCESS_OPTIONS}
                                </select> : null}
                            {processState.count >= 2 ?
                                <div style={{display:'flex'}}>
                                    <select
                                        onChange={(e) => {
                                            setProcessState((prevState) => ({...prevState, second: e.target.value}));
                                        }}
                                        className={classes.select}
                                        defaultValue={"GTAW"}
                                        value={processState.second}>
                                        {PROCESS_OPTIONS}
                                    </select>
                                    <img 
                                        src={deleteIcon}
                                        className={classes.deleteProcessBtn}
                                        alt="delete process button"
                                        onClick={() => deleteProcess(2)}/>
                                </div>
                                : null}
                            {processState.count === 3 ? 
                                <div style={{display:'flex'}}>
                                    <select
                                        onChange={(e) => {
                                            setProcessState((prevState) => ({...prevState, third: e.target.value}));
                                            console.log("here")
                                        }}
                                        className={classes.select}
                                        defaultValue={"GTAW"}
                                        value={processState.third}
                                        >
                                        {PROCESS_OPTIONS}
                                    </select>
                                    <img 
                                        src={deleteIcon} 
                                        className={classes.deleteProcessBtn}
                                        alt="delete process button"
                                        onClick={() => deleteProcess(3)}/>
                                </div>
                            : null}
                        </div>
                        {tableState.isProcessAvailable && processState.count !== 3 ? 
                            <img 
                                src={addIcon} 
                                className={classes.addProcessBtn}
                                alt="add process button"
                                onClick={addProcess}/>
                            : null}
                    </div>
                    
                    <div style={{display: "grid", gridTemplateColumns: processState.count===1 ? '140px 400px': processState.count===2 ? '140px 195px 180px' :'140px 135px 135px 125px'}}>
                        <div>
                            {currentTable !== null ? 
                                Object.keys(currentTable).map((key, idx) => {
                                    return (
                                    <div className={classes.tableCard}>
                                        <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={key} fullWidth variant="outlined" />
                                    </div>)
                                }) : null}
                        </div>
                        <div>
                            {currentTable !== null ? 
                                Object.keys(currentTable).map((key, idx) => {
                                    return makeCard(key, null)
                                }) : null}
                        </div>
                        {processState.count >= 2 ? 
                            <div className={classes.secondProcess}>
                                {currentTable !== null ? Object.keys(currentTable).map((key, idx) => {
                                        return makeCard(key, 1)
                                    }) : null}
                            </div> : null}
                        {processState.count===3 ? 
                            <div className={classes.thirdProcess}>
                                {currentTable !== null ? Object.keys(currentTable).map((key, idx) => {
                                        return makeCard(key, 2)
                                    }) : null}
                            </div> : null}
                    </div>                
                    </Paper>
                    <Grid container>
                        <Button className={classes.prevBtn} variant="outlined" onClick={() => moveTable(-1)}>이전테이블</Button>
                        <Button className={classes.submitTableBtn} variant="outlined" onClick={()=> submitTable()}>수정반영</Button>
                        <Button className={classes.nextBtn} variant="outlined" onClick={() => moveTable(1)}>다음테이블</Button>
                    </Grid>
                </div> : null}

                {tableState.title.includes("408") ? 
                <div>
                    <Paper elevation={1} className={classes.rightPaper}>
                    <div>
                        <div style={{display: "grid", gap: 10, gridTemplateColumns: '140px 130px 130px 120px'}}>
                            {tableState.isProcessAvailable ? 
                                <select
                                    onChange={(e) => {
                                        setProcessState((prevState) => ({...prevState, first: e.target.value}));
                                        console.log("here")
                                    }}
                                    className={classes.select}
                                    style={{
                                        marginLeft:10,
                                        marginTop:10
                                    }}
                                    defaultValue={"GTAW"}
                                    value={processState.first}
                                    >
                                    {PROCESS_OPTIONS}
                                </select> : null}
                            <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Shielding"} fullWidth variant="outlined" />
                            <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Backing"} fullWidth variant="outlined" />
                            <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Trailing"} fullWidth variant="outlined" />
                        </div>
                    </div>
                    <div style={{display: "grid", gridTemplateColumns: '140px 400px'}}>
                        <div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Gas"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Percent Composition"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Flow Rate"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Other"} fullWidth variant="outlined" />
                            </div>
                        </div>
                        <div style={{display: 'flex'}}>
                            {makeGasTable(null)}
                        </div>
                    </div>

                    {processState.count >= 2 ? <>
                    <div className={classes.secondGasProcess}>
                        <div className={classes.gasTable}>
                            {tableState.isProcessAvailable ? 
                                <select
                                onChange={(e) => {
                                    setProcessState((prevState) => ({...prevState, second: e.target.value}));
                                }}
                                className={classes.select}
                                style={{
                                    marginLeft:10,
                                    marginTop:10
                                }}
                                defaultValue={"GTAW"}
                                value={processState.second}
                                >
                                    {PROCESS_OPTIONS}
                                </select> : null}
                            
                            <div></div>
                            <div></div>
                            <img 
                                src={deleteIcon} 
                                className={classes.deleteProcessBtn} 
                                onClick={() => deleteProcess(2)}
                                alt="delete process button"/>
                        </div>
                    </div>
                    <div style={{display: "grid", gridTemplateColumns: '140px 400px'}}>
                        <div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Gas"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Percent Composition"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Flow Rate"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Other"} fullWidth variant="outlined" />
                            </div>
                        </div>
                        <div style={{display: 'flex'}}>
                            {makeGasTable(1)}
                        </div>         
                    </div>
                    </> : null}


                    {processState.count === 3 ? <>
                    <div className={classes.thirdGasProcess}>
                        <div className={classes.gasTable}>
                            {tableState.isProcessAvailable ? 
                                <select
                                    onChange={(e) => {
                                        setProcessState((prevState) => ({...prevState, third: e.target.value}));
                                    }}
                                    className={classes.select}
                                    style={{
                                        marginLeft:10,
                                        marginTop:10
                                    }}
                                    defaultValue={"GTAW"}
                                    value={processState.third}>
                                    {PROCESS_OPTIONS}
                                </select> : null}
                            <div/><div/>
                            <img 
                                src={deleteIcon} 
                                className={classes.deleteProcessBtn} 
                                onClick={() => deleteProcess(3)}
                                alt="delete process button"/>
                        </div>
                    </div>
                    <div style={{display: "grid", gridTemplateColumns: '140px 400px'}}>
                        <div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Gas"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Percent Composition"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Flow Rate"} fullWidth variant="outlined" />
                            </div>
                            <div className={classes.tableCard}>
                                <InputBase margin="dense" inputProps={{style: {fontSize: 10, fontWeight: 600}}} value={"Other"} fullWidth variant="outlined" />
                            </div>
                        </div>
                        <div style={{display: 'flex'}}>
                            {makeGasTable(2)}
                        </div>
                        
                    </div>
                    </> : null}
                    {tableState.isProcessAvailable && processState.count !== 3 ? 
                        <img 
                            src={addIcon} 
                            className={classes.addProcessBtn} 
                            alt="add process button" /> 
                        : null}           
                    </Paper>    
                    <Grid container>
                        <Button className={classes.prevBtn} variant="outlined" onClick={() => moveTable(-1)}>이전테이블</Button>
                        <Button className={classes.submitTableBtn} variant="outlined" onClick={()=> submitTable()}>수정반영</Button>
                        <Button className={classes.nextBtn} variant="outlined" onClick={() => moveTable(1)}>다음테이블</Button>
                    </Grid>
                </div> : null}
            </div>
        </div>
        <Box style={{marginTop: 10}}>Note</Box>
        <Paper elevation={1} className={classes.notePaper}>
            {ocrData !== null && ocrData["WPS_NOTES"] !== undefined ? ocrData["WPS_NOTES"]["fields"]["Instructions"].map((item) => {
                return <div style={{fontSize: 14}}>{item}<br></br></div>
            }) : null}
        </Paper>
        </>
    );
}