import * as React from 'react';
import { useState, useEffect } from 'react';
import { useHistory } from 'react-router';
import { useSnackbar } from 'notistack';
import axios from 'axios';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Dialog, DialogTitle, List, ListItem, CircularProgress, Typography, Box } from '@material-ui/core'
import CachedIcon from '@material-ui/icons/Cached';
import DoneIcon from '@material-ui/icons/Done';

const useStyles = makeStyles((theme) => ({
    title: {
        display: 'flex', 
        marginBottom: 10
    },
    controller: {
        display: 'flex',
        marginTop: 20, 
        marginLeft: 10,
        gap: 20
    },
    uploadBtn: {
        width: 292, 
        height: 35, 
        background: "rgba(23, 62, 139, 1)", 
        color: "white"
    },
    dialogContent: {
        margin: 10
    },
    version: {
        marginLeft: 'auto', 
        marginTop: 'auto', 
        color: 'white', 
        fontSize: 7
    }
}));

export default function Uploader(props) {
    const classes = useStyles();
    const history = useHistory();
    const { enqueueSnackbar, closeSnackbar } = useSnackbar();
    const [tempFile, setTempFile] = useState(null);
    const [loadingDialogOpen, setLoadingDialogOpen] = useState(false);
    const [uploadState, setUploadState] = useState({
        upload: false,
        ocr: false
    });

    const handleFile = (e) => {
        e.preventDefault();
        setTempFile(e.target.files[0]);
    }

    const submitFile = (e) => {
        e.preventDefault();
        if (tempFile !== null) {
            props.getFile(tempFile);
            setLoadingDialogOpen(true);
            axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                "action": "UPLOAD",
                "file": tempFile.name
            })
            .then((res)=> {
                axios.put(res.data.body.upload_url, tempFile)
                .then((res) => {
                    setUploadState({...uploadState, upload: true});
                    axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                        "action": "OCR",
                        "file": tempFile.name
                    }, {headers : {"InvocationType":"Event"}})
                    .then((res) => {
                        const timer = setInterval(() => {
                            axios.post("https://7ps0j0pj49.execute-api.ap-northeast-2.amazonaws.com/v1/API_Lambda", {
                                "action": "RESULT",
                                "file": tempFile.name
                            })
                            .then((res) => {
                                if (res.data.body.result === "OK") {
                                    clearInterval(timer);
                                    props.getOcrData(res.data.body.data[0]);
                                    setUploadState({...uploadState, ocr: true});
                                    history.push('/inspect');
                                }
                            })
                        }, 5000);
                    })
                })
            })
            .catch((err)=> {
                console.log(err);
                setLoadingDialogOpen(false);
                enqueueSnackbar('에러 발생', {variant: 'error'});
            })
        } else {
            enqueueSnackbar('파일이 선택되지 않았습니다', {variant: 'error'});
        }
    }

    return (
        <>
        <div className={classes.title}>
            <Box fontWeight="fontWeightBold" fontSize={24} m={1}>
                Upload Data
            </Box>
        </div>
        
        <div className={classes.controller}>
            <Button variant="outlined" component="label" className={classes.uploadBtn}>
                <input type="file" style={{display:"none"}} accept="application/pdf" onChange={handleFile} />
                {tempFile==null ? "업로드" : "업로드 : "+ tempFile.name}
            </Button>
            <Button variant="outlined" color="primary" onClick={submitFile}>
                <CachedIcon fontSize='small'/>&nbsp;&nbsp;실행
            </Button> 
        </div>

        <Dialog open={loadingDialogOpen} aria-labelledby="upload-dialog" maxWidth='xs' fullWidth={true}>
            <DialogTitle id="upload-dialog">업로드 및 OCR</DialogTitle>
            <List className={classes.dialogContent}>
                <ListItem>
                    <Typography variant="h6" noWrap >1) 업로드 진행 &nbsp;&nbsp;</Typography>
                    {!uploadState.upload ? <CircularProgress size={20}/> : <DoneIcon />}
                </ListItem>
                <ListItem>
                    <Typography variant="h6" noWrap >2) OCR 진행 &nbsp;&nbsp;&nbsp;</Typography>
                    {!uploadState.ocr ? <CircularProgress size={20}/> : <DoneIcon />}
                </ListItem>
            </List>
        </Dialog>
        <div className={classes.version}>21/12/28-14:50</div>
        </>
    );
}